  import React from 'react';
  import bread from './bread.jpg';
   import Card from '@material-ui/core/Card';
   import { makeStyles } from '@material-ui/core/styles';
  import ExpansionPanel from '@material-ui/core/ExpansionPanel';
import ExpansionPanelSummary from '@material-ui/core/ExpansionPanelSummary';
import ExpansionPanelDetails from '@material-ui/core/ExpansionPanelDetails';
import Typography from '@material-ui/core/Typography';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import Button from '@material-ui/core/Button';
import { Link } from "react-router-dom";


const useStyles = makeStyles(theme => ({
  root: {
    width: '15%',
  },
  heading: {
    fontSize: theme.typography.pxToRem(15),
    fontWeight: theme.typography.fontWeightRegular,
  },
})); 
   
  const Logo = () => {
     const classes = useStyles();
    return(
      <div>
      <div>
      <Card>
      <h3>product type</h3>
      </Card>
      </div>

        
 <div className="Tilt-inner" >
 <Card>
 <img style={{paddingTop:'5px',height:300,width:400}} alt='logo' src={bread}/>
 </Card>
  </div>

   
      <div className='center'>
       <Card>
        <h2>style name:with no cost EMI/Additional exchange Offers</h2>

  </Card>
      </div>

 <ExpansionPanel>
        <ExpansionPanelSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel2a-content"
          id="panel2a-header"
        >
          <Typography className={classes.heading}>Size</Typography>
        </ExpansionPanelSummary>
        <ExpansionPanelDetails>
          <Typography>
           1 kg
          </Typography>
         
        </ExpansionPanelDetails>
          <ExpansionPanelDetails>
          <Typography>
           2 kg
          </Typography>
         
        </ExpansionPanelDetails>
      </ExpansionPanel>


       <ExpansionPanel>
        <ExpansionPanelSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel2a-content"
          id="panel2a-header"
        >

           
          <Typography className={classes.heading}>delivery</Typography>
        </ExpansionPanelSummary>
        <ExpansionPanelDetails>
          <Typography>
           cash on delivery
          </Typography>
         
         
        </ExpansionPanelDetails>
          <ExpansionPanelDetails>
          <Typography>
           online paymnt
          </Typography>
         
        </ExpansionPanelDetails>
      </ExpansionPanel>
     

<Card>
      <h2>M.R.P :100
          Deal of the day     
      </h2>
</Card>

      <Link to='cart'>
<Button variant="contained" className={classes.button}>
       Buy Now
        
        
      </Button>
   
<Button variant="contained" className={classes.button}>
        Add to Cart
        
        
      </Button>
  </Link>


        </div>
      
      
        );

  }

export default Logo;